},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"cajon color-acento-botones p-4 mb-5 fnd-1"},[_c('p',{staticClass:"mb-0"},[_vm._v("Relacionarse con los demás puede ser complicado por diferencias culturales, sociales y personales, pero es fundamental evitar prejuicios para no perder oportunidades de aprendizaje y conocer personas interesantes. Aunque las primeras impresiones pueden llevar a interpretaciones erróneas, las personas comparten necesidades comunes de afecto, participación y contribución. Al superar las apariencias y conectar desde la empatía, descubrimos que las diferencias no son tantas; una conversación positiva se convierte en una valiosa caricia psicológica.")])])
